/**
 * CategoryCard -     
 *     
 */
import React from 'react';
import { CategoryData } from '../../../../types/category';
import './styles.scss';

interface CategoryCardProps {
  category: CategoryData;
  onClick: () => void;
}

const CategoryCard: React.FC<CategoryCardProps> = ({ category, onClick }) => {
  const getCardClass = () => {
    switch (category.status) {
      case 'caution':
        return 'card-caution';
      case 'normal':
        return 'card-normal';
      case 'no_data':
        return 'card-no-data';
      default:
        return 'card-normal';
    }
  };
  
  const getStatusText = () => {
    switch (category.status) {
      case 'caution':
        return '';
      case 'normal':
        return '';
      case 'no_data':
        return '';
      default:
        return '';
    }
  };
  
  return (
    <div 
      className={`category-card ${getCardClass()}`}
      onClick={onClick}
      role="button"
      tabIndex={0}
      aria-label={`${category.name} , ${getStatusText()}`}
      onKeyDown={(e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          onClick();
        }
      }}
    >
      <div className="card-content">
        <span className="card-name">{category.name}</span>
        <span className={`card-status ${category.status}`}>
          {getStatusText()}
        </span>
      </div>
    </div>
  );
};

export default CategoryCard;
