import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import AuthForm from '../components/AuthForm';
import '../styles/_auth.scss';

const AuthPage: React.FC = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const handleBack = () => {
    // 1: location.state from  
    const from = (location.state as any)?.from;
    
    // 2:  URL     (UUID hospital    )
    const urlParams = new URLSearchParams(location.search);
    const uuid = urlParams.get('uuid');
    const hospital = urlParams.get('hospital') || urlParams.get('hospitalId');
    const layout = urlParams.get('layout');
    
    if (from) {
      console.log(' []   :', from);
      const targetUrl = `${window.location.origin}/welno${from}`;
      window.location.href = targetUrl;
    } else if (uuid && hospital) {
      console.log(' [] UUID hospital   ');
      // React Router navigate  basename   
      const queryString = `uuid=${uuid}&hospital=${hospital}${layout ? `&layout=${layout}` : ''}`;
      navigate(`/?${queryString}`);
    } else {
      console.log(' []   ');
      // React Router    
      navigate('/');
    }
  };

  return (
    <div className="questionnaire-container tilko-login">
      <div className="container bg_auth_yellow">
        <div className="wrapper login">
          <AuthForm onBack={handleBack} />
        </div>
      </div>
    </div>
  );
};

export default AuthPage;