import React, { useState } from 'react';
import './styles.scss';

interface EmailInputModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (email: string) => Promise<void>;
  loading?: boolean;
  initialEmail?: string;
}

export const EmailInputModal: React.FC<EmailInputModalProps> = ({
  isOpen,
  onClose,
  onSubmit,
  loading = false,
  initialEmail = '',
}) => {
  const [email, setEmail] = useState(initialEmail);
  const [error, setError] = useState('');

  if (!isOpen) return null;

  const handleSubmit = async () => {
    //   
    if (!email || !email.includes('@')) {
      setError('   .');
      return;
    }

    //   
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setError('    .');
      return;
    }

    setError('');
    try {
      await onSubmit(email);
    } catch (err) {
      setError('    .');
      console.error('Email submit error:', err);
    }
  };

  const handleClose = () => {
    setEmail(initialEmail);
    setError('');
    onClose();
  };

  return (
    <div className="email-input-modal-overlay" onClick={handleClose}>
      <div className="email-input-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h2 className="modal-title"> </h2>
          <button className="modal-close-button" onClick={handleClose} type="button" aria-label="">
            
          </button>
        </div>
        
        <div className="modal-content">
          <p className="modal-description">
             (PDF)    .
          </p>
          
          <div className="email-input-group">
            <input
              type="email"
              placeholder="example@email.com"
              value={email}
              onChange={(e) => {
                setEmail(e.target.value);
                setError('');
              }}
              disabled={loading}
              onKeyPress={(e) => {
                if (e.key === 'Enter' && !loading && email) {
                  handleSubmit();
                }
              }}
            />
            {error && <p className="error-message">{error}</p>}
          </div>
          
          <div className="modal-actions">
            <button
              className="action-button cancel-button"
              onClick={handleClose}
              type="button"
              disabled={loading}
            >
              
            </button>
            <button
              className="action-button submit-button"
              onClick={handleSubmit}
              type="button"
              disabled={loading || !email}
            >
              {loading ? ' ...' : ' '}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EmailInputModal;
