//  API 
export const API_HOSTS = {
  LOCAL: 'http://localhost:8082',
  PRODUCTION: 'https://welno.kindhabit.com'
} as const;

//    API  
const getApiHost = (endpoint: 'local' | 'production' = 'local'): string => {
  return endpoint === 'local' ? API_HOSTS.LOCAL : API_HOSTS.PRODUCTION;
};

