/**
 * useFloatingButton Hook
 * 
 *      
 * 
 *  :
 * - REPORT_READY  "  "
 * - REPORT_EXPIRED  " "
 * - REPORT_PENDING  "  ..."
 * - PAYMENT_REQUIRED  " "
 * - ACTION_REQUIRED*  "  "
 * - TERMS_REQUIRED*  " "
 */

import { useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import type { UnifiedStatus } from '../../../hooks/useUnifiedStatus';

//    
export interface FloatingButtonConfig {
  visible: boolean;
  text: string;
  action: () => void;
}

export function useFloatingButton(
  unifiedStatus: UnifiedStatus | null,
  uuid: string,
  partnerId: string | null
): FloatingButtonConfig {
  const navigate = useNavigate();

  const getConfig = useCallback((): FloatingButtonConfig => {
    if (!unifiedStatus) {
      return { visible: false, text: '', action: () => {} };
    }

    const { status } = unifiedStatus;

    // 1. REPORT_READY  "  " ()
    if (status === 'REPORT_READY') {
      return {
        visible: true,
        text: '  ',
        action: () => {
          console.log('[ ] :   (  )');
          //        
          //     (: document.getElementById('report-detail')?.scrollIntoView())
        },
      };
    }

    // 2. REPORT_EXPIRED  " "
    if (status === 'REPORT_EXPIRED') {
      return {
        visible: true,
        text: ' ',
        action: () => {
          console.log('[ ] :  ');
          // TODO:   API 
          alert('    .');
        },
      };
    }

    // 3. REPORT_PENDING  "  ..." ()
    if (status === 'REPORT_PENDING') {
      return {
        visible: true,
        text: '  ...',
        action: () => {
          console.log('[ ] :   ( )');
        },
      };
    }

    // 4. PAYMENT_REQUIRED  " "
    if (status === 'PAYMENT_REQUIRED') {
      return {
        visible: true,
        text: ' ',
        action: () => {
          console.log('[ ] :   ');
          const paymentUrl = `/campaigns/disease-prediction?page=payment&uuid=${uuid}&partner=${partnerId || ''}`;
          navigate(paymentUrl);
        },
      };
    }

    // 5. ACTION_REQUIRED*  "  "
    if (status === 'ACTION_REQUIRED' || status === 'ACTION_REQUIRED_PAID') {
      return {
        visible: true,
        text: '  ',
        action: () => {
          console.log('[ ] : Tilko ');
          // TODO: Tilko   
          navigate(`/login?return_to=/disease-report&uuid=${uuid}&mode=campaign`);
        },
      };
    }

    // 6. TERMS_REQUIRED*  " "
    if (status.startsWith('TERMS_REQUIRED')) {
      return {
        visible: true,
        text: ' ',
        action: () => {
          console.log('[ ] :   ');
          const termsUrl = `/campaigns/disease-prediction?page=terms&uuid=${uuid}&partner=${partnerId || ''}`;
          navigate(termsUrl);
        },
      };
    }

    // 7.     
    return { visible: false, text: '', action: () => {} };
  }, [unifiedStatus, uuid, partnerId, navigate]);

  const config = getConfig();

  // App.tsx FloatingButton   (Custom Event )
  useEffect(() => {
    const event = new CustomEvent('floating-button-config', {
      detail: config,
    });
    window.dispatchEvent(event);

    console.log(`[  ] visible=${config.visible}, text="${config.text}"`);
  }, [config]);

  return config;
}
