/**
 * CategoryView -     
 *        
 */
import React, { useState, useMemo } from 'react';
import { TilkoHealthCheckupRaw } from '../../../types/health';
import { processHealthDataToCategories } from '../../../utils/categoryDataProcessor';
import CategoryCardsGrid from './CategoryCardsGrid';
import CategoryDetailView from './CategoryDetailView';
import ItemTrendModal from './ItemTrendModal';
import './styles.scss';

interface CategoryViewProps {
  healthData: TilkoHealthCheckupRaw[];
  year?: string;
  compact?: boolean;
  patientName?: string;
  healthAge?: number;
  actualAge?: number;
  showHealthAgeGlow?: boolean; //   
  onCategoryClick?: (categoryId: string) => void; //   ()
  onDepthChange?: (depth: number) => void; //    (0: , 1: , 2: )
  onBackRequest?: () => void; //     
}

const CategoryView: React.FC<CategoryViewProps> = ({
  healthData,
  year,
  compact = false,
  patientName,
  healthAge,
  actualAge,
  showHealthAgeGlow = false,
  onCategoryClick,
  onDepthChange,
  onBackRequest
}) => {
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [selectedItem, setSelectedItem] = useState<string | null>(null);
  
  //      ( )
  const availableYears = useMemo(() => {
    const years = healthData.map(d => d.Year.replace('', '')).filter(Boolean);
    //     ()
    return Array.from(new Set(years)).sort((a, b) => parseInt(b) - parseInt(a));
  }, [healthData]);
  
  //    (props  year   )
  const [selectedYear, setSelectedYear] = useState<string>(() => {
    if (year) return year;
    return availableYears[0] || new Date().getFullYear().toString();
  });
  
  //    (  )
  const categories = useMemo(() => {
    return processHealthDataToCategories(healthData, selectedYear);
  }, [healthData, selectedYear]);
  
  //   (  )
  const currentYear = useMemo(() => {
    return selectedYear;
  }, [selectedYear]);
  
  //   
  const handleCategoryClick = (categoryId: string) => {
    if (onCategoryClick) {
      onCategoryClick(categoryId);
    }
    setSelectedCategory(categoryId);
  };
  
  //     
  React.useEffect(() => {
    if (onDepthChange) {
      if (selectedItem) {
        onDepthChange(2); // 
      } else if (selectedCategory) {
        onDepthChange(1); //  
      } else {
        onDepthChange(0); // 
      }
    }
  }, [selectedCategory, selectedItem, onDepthChange]);

  //      ( )
  React.useEffect(() => {
    const handleExternalBack = () => {
      // 1:    
      if (selectedItem) {
        setSelectedItem(null);
        return;
      }
      // 2:    
      if (selectedCategory) {
        setSelectedCategory(null);
      }
    };
    window.addEventListener('categoryViewBack', handleExternalBack);
    return () => {
      window.removeEventListener('categoryViewBack', handleExternalBack);
    };
  }, [selectedCategory, selectedItem]);

  //    (2 :       )
  const handleBack = () => {
    // 1:    
    if (selectedItem) {
      setSelectedItem(null);
      return;
    }
    // 2:    
    if (selectedCategory) {
      setSelectedCategory(null);
    }
  };
  
  //   
  const handleItemClick = (itemName: string) => {
    setSelectedItem(itemName);
  };
  
  //   
  const handleCloseModal = () => {
    setSelectedItem(null);
  };
  
  //   
  const handleYearChange = (newYear: string) => {
    setSelectedYear(newYear);
  };
  
  return (
    <div className="category-view">
      {!selectedCategory ? (
        <CategoryCardsGrid
          categories={categories}
          onCategoryClick={handleCategoryClick}
          compact={compact}
          showHealthAge={!compact && (healthAge !== undefined && healthAge !== null) && (actualAge !== undefined && actualAge !== null)}
          healthAge={healthAge}
          actualAge={actualAge}
          patientName={patientName}
          showHealthAgeGlow={showHealthAgeGlow}
        />
      ) : (
        <CategoryDetailView
          categoryId={selectedCategory}
          healthData={healthData}
          year={currentYear}
          availableYears={availableYears}
          onYearChange={handleYearChange}
          onBack={handleBack}
          onItemClick={handleItemClick}
          patientName={patientName}
          healthAge={healthAge}
          actualAge={actualAge}
        />
      )}
      
      {selectedItem && (
        <ItemTrendModal
          itemName={selectedItem}
          healthData={healthData}
          onClose={handleCloseModal}
          patientName={patientName}
          showRangeIndicator={true}
        />
      )}
    </div>
  );
};

export default CategoryView;
