export interface HealthItemConfig {
  tilkoName: string;           // Tilko Item  (: "")
  displayName: string;         //   (: "BMI")
  category: string;            //  ID (: "body")
  unit: string;                //  (: "kg/m")
  fieldName: string;           //  (: "bmi")
  showInTrends: boolean;       //   
  valueType?: 'number' | 'string' | 'blood_pressure';  //  
  extract?: 'systolic' | 'diastolic';  //   
}

export interface CategoryConfig {
  id: string;
  name: string;
  illnesses: string[];  // Tilko Illness 
  icon: string;
}

