/**
 *     
 * Redis      
 */
import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import './CollectingDataPage.scss';

interface CollectionProgress {
  sessionId: string;
  status: string;
  message: string;
  progress: number;
  currentStep: string;
  isCompleted: boolean;
  hasError: boolean;
  errorMessage?: string;
}

const CollectingDataPage: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  
  const [progress, setProgress] = useState<CollectionProgress>({
    sessionId: '',
    status: 'initializing',
    message: '   ...',
    progress: 0,
    currentStep: '',
    isCompleted: false,
    hasError: false
  });

  const [dots, setDots] = useState('');

  // URL  
  const urlParams = new URLSearchParams(location.search);
  const uuid = urlParams.get('uuid');
  const hospital = urlParams.get('hospital');
  const sessionId = urlParams.get('session');

  //  
  useEffect(() => {
    const interval = setInterval(() => {
      setDots(prev => {
        if (prev.length >= 3) return '';
        return prev + '.';
      });
    }, 500);

    return () => clearInterval(interval);
  }, []);

  //   
  const checkCollectionStatus = useCallback(async () => {
    if (!sessionId) {
      console.error('[]  ID ');
      return;
    }

    try {
      const response = await fetch(`/api/v1/tilko/session/status/${sessionId}`);
      
      if (!response.ok) {
        throw new Error(`API  : ${response.status}`);
      }

      const result = await response.json();
      console.log('[]  :', result);

      if (!result.success) {
        throw new Error(result.message || '   ');
      }

      const status = result.status;
      let newProgress: CollectionProgress = {
        sessionId: sessionId,
        status: status,
        message: '  ...',
        progress: 0,
        currentStep: ' ',
        isCompleted: false,
        hasError: false
      };

      //     
      switch (status) {
        case 'authenticated':
        case 'auth_completed':
          newProgress.progress = 10;
          newProgress.currentStep = ' ';
          newProgress.message = ' .   ...';
          break;

        case 'fetching_health_data':
          newProgress.progress = 30;
          newProgress.currentStep = '  ';
          newProgress.message = '   ...';
          break;

        case 'fetching_prescription_data':
          newProgress.progress = 70;
          newProgress.currentStep = '  ';
          newProgress.message = '   ...';
          break;

        case 'completed':
          newProgress.progress = 100;
          newProgress.currentStep = ' ';
          newProgress.message = '   !';
          newProgress.isCompleted = true;
          break;

        case 'info_required':
          newProgress.hasError = true;
          newProgress.currentStep = '  ';
          newProgress.message = '  .';
          //     
          const errorMessages = result.messages || [];
          const lastError = errorMessages[errorMessages.length - 1];
          if (lastError && lastError.message) {
            const errorMsg = typeof lastError.message === 'object' 
              ? lastError.message.message || lastError.message.title || '  .'
              : lastError.message;
            newProgress.errorMessage = errorMsg;
          } else {
            newProgress.errorMessage = ', ,      .';
          }
          //    
          setTimeout(() => {
            console.log('[]    -   ');
            navigate(`/login?uuid=${uuid}&hospital=${hospital}&info_required=true`);
          }, 3000);
          break;

        case 'error':
          newProgress.hasError = true;
          newProgress.currentStep = ' ';
          newProgress.message = '    .';
          //     
          const errorMsgs = result.messages || [];
          const lastErr = errorMsgs[errorMsgs.length - 1];
          if (lastErr && lastErr.message) {
            const errMsg = typeof lastErr.message === 'object' 
              ? lastErr.message.message || lastErr.message.title || '   '
              : lastErr.message;
            
            //        
            if (typeof lastErr.message === 'object' && lastErr.message.requires_info_recheck) {
              newProgress.errorMessage = errMsg;
              setTimeout(() => {
                console.log('[]    -   ');
                navigate(`/login?uuid=${uuid}&hospital=${hospital}&info_required=true`);
              }, 3000);
            } else {
              newProgress.errorMessage = errMsg;
              //       5    
              setTimeout(() => {
                console.log('[]   -   ');
                navigate('/');
              }, 5000);
            }
          } else {
            newProgress.errorMessage = result.error_message || '   ';
            //     5    
            setTimeout(() => {
              console.log('[]   -   ');
              navigate('/');
            }, 5000);
          }
          break;

        default:
          newProgress.progress = 5;
          newProgress.currentStep = '';
          newProgress.message = '   ...';
      }

      setProgress(newProgress);

      //     -  uuid hospital 
      if (newProgress.isCompleted) {
        setTimeout(async () => {
          console.log('[]   -   ');
          
          // URL  
          let finalUuid = uuid;
          let finalHospital = hospital;
          
          // URL    
          if (!finalUuid || !finalHospital) {
            try {
              const savedSessionId = localStorage.getItem('tilko_session_id');
              if (savedSessionId) {
                //   API  patient_uuid hospital_id 
                const response = await fetch(`/api/v1/tilko/session/status/${savedSessionId}`);
                if (response.ok) {
                  const result = await response.json();
                  if (result.success && result.patient_uuid && result.hospital_id) {
                    finalUuid = result.patient_uuid;
                    finalHospital = result.hospital_id;
                    console.log('[]  UUID/Hospital :', { uuid: finalUuid, hospital: finalHospital });
                  }
                }
              }
              
              //  API   localStorage   
              if (!finalUuid || !finalHospital) {
                const savedSessionData = localStorage.getItem('tilko_session_data');
                if (savedSessionData) {
                  try {
                    const sessionData = JSON.parse(savedSessionData);
                    finalUuid = finalUuid || sessionData?.patient_uuid || sessionData?.user_info?.patient_uuid;
                    finalHospital = finalHospital || sessionData?.hospital_id || sessionData?.user_info?.hospital_id;
                    console.log('[] localStorage   UUID/Hospital :', { uuid: finalUuid, hospital: finalHospital });
                  } catch (e) {
                    console.warn('[]    :', e);
                  }
                }
              }
            } catch (error) {
              console.warn('[]  UUID/Hospital  :', error);
            }
          }
          
          if (finalUuid && finalHospital) {
            navigate(`/results-trend?uuid=${finalUuid}&hospital=${finalHospital}`);
          } else {
            console.warn('[] UUID/Hospital  -    ( )');
            navigate('/results-trend');
          }
        }, 2000);
      }

    } catch (error) {
      console.error('[]   :', error);
      setProgress(prev => ({
        ...prev,
        hasError: true,
        currentStep: ' ',
        message: '   .',
        errorMessage: error instanceof Error ? error.message : '   '
      }));
      
      //    5    
      setTimeout(() => {
        console.log('[]   -   ');
        navigate('/');
      }, 5000);
    }
  }, [sessionId, uuid, hospital, navigate]);

  //      
  useEffect(() => {
    if (!sessionId) {
      console.error('[]  ID ');
      navigate('/');
      return;
    }

    //    
    checkCollectionStatus();

    // 2  
    const interval = setInterval(checkCollectionStatus, 2000);

    return () => clearInterval(interval);
  }, [sessionId, checkCollectionStatus, navigate]);

  const handleRetry = () => {
    console.log('[] ');
    setProgress(prev => ({
      ...prev,
      hasError: false,
      errorMessage: undefined
    }));
    checkCollectionStatus();
  };

  const handleGoHome = () => {
    console.log(' []  ');
    //  
    localStorage.removeItem('tilko_session_id');
    localStorage.removeItem('tilko_session_data');
    navigate('/');
  };

  return (
    <div className="collecting-data-page">
      <div className="collecting-container">
        <div className="collecting-header">
          <h1>  </h1>
          <p>  .    .</p>
        </div>

        <div className="progress-section">
          {/*   */}
          <div className="progress-bar-container">
            <div className="progress-bar">
              <div 
                className="progress-fill"
                style={{ width: `${progress.progress}%` }}
              />
            </div>
            <div className="progress-text">
              {progress.progress}%
            </div>
          </div>

          {/*   */}
          <div className="current-step">
            <div className="step-icon">
              {progress.hasError ? (
                <span className="error-icon"></span>
              ) : progress.isCompleted ? (
                <span className="success-icon"></span>
              ) : (
                <div className="loading-spinner" />
              )}
            </div>
            <div className="step-info">
              <h3>{progress.currentStep}</h3>
              <p>{progress.message}{!progress.hasError && !progress.isCompleted && dots}</p>
              {progress.errorMessage && (
                <p className="error-message">{progress.errorMessage}</p>
              )}
            </div>
          </div>

          {/*   */}
          <div className="steps-checklist">
            <div className={`step-item ${progress.progress >= 10 ? 'completed' : 'pending'}`}>
              <span className="step-number">1</span>
              <span className="step-label"> </span>
              {progress.progress >= 10 && <span className="check-mark"></span>}
            </div>
            <div className={`step-item ${progress.progress >= 30 ? 'completed' : progress.progress >= 10 ? 'active' : 'pending'}`}>
              <span className="step-number">2</span>
              <span className="step-label">  </span>
              {progress.progress >= 70 && <span className="check-mark"></span>}
            </div>
            <div className={`step-item ${progress.progress >= 70 ? 'completed' : progress.progress >= 30 ? 'active' : 'pending'}`}>
              <span className="step-number">3</span>
              <span className="step-label">  </span>
              {progress.progress >= 100 && <span className="check-mark"></span>}
            </div>
            <div className={`step-item ${progress.progress >= 100 ? 'completed' : progress.progress >= 70 ? 'active' : 'pending'}`}>
              <span className="step-number">4</span>
              <span className="step-label">  </span>
              {progress.progress >= 100 && <span className="check-mark"></span>}
            </div>
          </div>
        </div>

        {/*   */}
        <div className="action-buttons">
          {progress.hasError && (
            <>
              <button onClick={handleRetry} className="retry-button">
                 
              </button>
              <button onClick={handleGoHome} className="home-button">
                
              </button>
            </>
          )}
          {progress.isCompleted && (
            <button 
              onClick={() => navigate(`/results-trend?uuid=${uuid}&hospital=${hospital}`)}
              className="view-results-button"
            >
               
            </button>
          )}
        </div>

        {/*   */}
        <div className="info-message">
          <p>    .</p>
          <p>    .</p>
          <p>  1-2  .</p>
        </div>
      </div>
    </div>
  );
};

export default CollectingDataPage;

