import React, { useEffect, useRef } from 'react';
import './AuthInput.scss';

export type InputFieldType = 'name' | 'phone' | 'birthday';

export interface AuthInputProps {
  type: InputFieldType;
  value: string;
  onChange: (value: string) => void;
  onComplete?: () => void;
  disabled?: boolean;
  autoFocus?: boolean;
  placeholder?: string;
  error?: string;
}

/**
 * AuthInput 
 * , ,    .
 */
const AuthInput: React.FC<AuthInputProps> = ({
  type,
  value,
  onChange,
  onComplete,
  disabled = false,
  autoFocus = false,
  placeholder,
  error,
}) => {
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (autoFocus && inputRef.current) {
      inputRef.current.focus();
    }
  }, [autoFocus]);

  const getPlaceholder = () => {
    if (placeholder) return placeholder;
    
    switch (type) {
      case 'name':
        return ' ';
      case 'phone':
        return '   (- )';
      case 'birthday':
        return ' 8 (: 19900101)';
      default:
        return '';
    }
  };

  const getLabel = () => {
    switch (type) {
      case 'name':
        return ' ';
      case 'phone':
        return '  ';
      case 'birthday':
        return ' ';
      default:
        return '';
    }
  };

  const getDescription = () => {
    switch (type) {
      case 'name':
        return ' ?\n    ';
      case 'phone':
        return '    ';
      case 'birthday':
        return ' 8 \n) 1990 1 1  19900101';
      default:
        return '';
    }
  };

  const formatValue = (rawValue: string): string => {
    switch (type) {
      case 'phone':
        //  
        const digits = rawValue.replace(/\D/g, '');
        //  11
        return digits.slice(0, 11);
      case 'birthday':
        //  
        const bdayDigits = rawValue.replace(/\D/g, '');
        //  8
        return bdayDigits.slice(0, 8);
      case 'name':
        //   50
        return rawValue.slice(0, 50);
      default:
        return rawValue;
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const formatted = formatValue(e.target.value);
    onChange(formatted);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && onComplete) {
      e.preventDefault();
      onComplete();
    }
  };

  const getInputType = () => {
    return type === 'phone' || type === 'birthday' ? 'tel' : 'text';
  };

  const getInputMode = (): 'text' | 'tel' | 'numeric' | 'decimal' | 'search' | 'email' | 'url' | undefined => {
    return type === 'phone' || type === 'birthday' ? 'numeric' : 'text';
  };

  return (
    <div className="auth-input">
      <div className="auth-input__header">
        <h2 className="auth-input__label">{getLabel()}</h2>
      </div>
      
      <div className="auth-input__description">
        {getDescription().split('\n').map((line, index) => (
          <p key={index}>{line}</p>
        ))}
      </div>

      <div className="auth-input__field-wrapper">
        <input
          ref={inputRef}
          type={getInputType()}
          inputMode={getInputMode()}
          className={`auth-input__field ${error ? 'auth-input__field--error' : ''}`}
          value={value}
          onChange={handleChange}
          onKeyDown={handleKeyDown}
          placeholder={getPlaceholder()}
          disabled={disabled}
          autoComplete="off"
        />
        {error && <p className="auth-input__error">{error}</p>}
      </div>
    </div>
  );
};

export default AuthInput;
