//   

export interface AgeCalculationResult {
  currentAge: number;
  ageDifference: number; //  -  (   )
  isHealthier: boolean; //    true
}

/**
 *     
 * @param birthday  (YYYY-MM-DD  YYYYMMDD )
 * @returns   ( )
 */
export const calculateCurrentAge = (birthday: string | null): number | null => {
  if (!birthday) return null;

  try {
    let birthDate: Date;
    
    // YYYY-MM-DD  
    if (birthday.includes('-')) {
      birthDate = new Date(birthday);
    } 
    // YYYYMMDD  
    else if (birthday.length === 8) {
      const year = parseInt(birthday.substring(0, 4));
      const month = parseInt(birthday.substring(4, 6)) - 1; //  0 
      const day = parseInt(birthday.substring(6, 8));
      birthDate = new Date(year, month, day);
    } 
    else {
      return null;
    }

    if (isNaN(birthDate.getTime())) {
      return null;
    }

    const today = new Date();
    let age = today.getFullYear() - birthDate.getFullYear();
    const monthDiff = today.getMonth() - birthDate.getMonth();
    
    //     1 
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthDate.getDate())) {
      age--;
    }

    return age;
  } catch (error) {
    console.error('  :', error);
    return null;
  }
};

/**
 *   
 * @param bodyAge 
 * @param currentAge 
 * @returns  
 */
export const compareAges = (
  bodyAge: number,
  currentAge: number | null
): AgeCalculationResult | null => {
  if (currentAge === null) return null;

  const ageDifference = bodyAge - currentAge;
  const isHealthier = ageDifference < 0;

  return {
    currentAge,
    ageDifference: Math.abs(ageDifference),
    isHealthier,
  };
};




