/**
 * JudgmentCard -    
 *  
 */
import React from 'react';
import './styles.scss';

interface JudgmentCardProps {
  patientName: string;
  judgment: string;        // ,  
  description?: string;
  icon?: string;
  className?: string;
}

const JudgmentCard: React.FC<JudgmentCardProps> = ({
  patientName,
  judgment,
  description,
  icon = '',
  className = ''
}) => {
  //     
  const getJudgmentClass = () => {
    if (judgment?.includes('')) return 'judgment-caution';
    if (judgment?.includes('')) return 'judgment-warning';
    if (judgment?.includes('')) return 'judgment-normal';
    return '';
  };
  
  return (
    <div className={`judgment-card ${getJudgmentClass()} ${className}`}>
      <div className="judgment-header">
        <div className="judgment-text-wrapper">
          <span className="judgment-text">
            {patientName}  {' '}
            <strong className="judgment-result">{judgment}</strong>
          </span>
          <span className="judgment-help-icon" title="   ">?</span>
        </div>
        <span className="judgment-icon" role="img" aria-label=" ">
          {icon}
        </span>
      </div>
      {description && (
        <p className="judgment-description">{description}</p>
      )}
    </div>
  );
};

export default JudgmentCard;
