import React, { useMemo, useEffect, useState, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { PartnerStatus } from './index';
import { API_ENDPOINTS } from '../../config/api';
import { checkAllTermsAgreement } from '../../utils/termsAgreement';
import './styles/landing.scss';

//      

interface Props {
  status?: PartnerStatus | null;
}

const LandingPage: React.FC<Props> = ({ status }) => {
  const { search } = useLocation();
  const navigate = useNavigate();
  const query = useMemo(() => new URLSearchParams(search), [search]);
  const [isGenerating, setIsGenerating] = useState(false);
  const [countdown, setCountdown] = useState<number | null>(null);

  //         
  useEffect(() => {
    console.log(' [LandingPage] ===== LandingPage   =====');
    console.log(' [LandingPage]  :');
    console.log(' [LandingPage] -  : " 10   4    ..."');
    console.log(' [LandingPage] -  ""    (, ,  )');
    
    //   (smooth  auto )
    window.scrollTo({ top: 0, behavior: 'auto' });
    
    // 3  
    setCountdown(3);
    
    //     
    window.dispatchEvent(new CustomEvent('welno-campaign-button-text', { 
      detail: { text: '3  ' } 
    }));
    
    const countdownInterval = setInterval(() => {
      setCountdown((prev) => {
        if (prev === null || prev <= 1) {
          clearInterval(countdownInterval);
          return 0; // 0    
        }
        const newCount = prev - 1;
        //    
        let buttonText = '';
        if (newCount === 2) {
          buttonText = '2  ';
        } else if (newCount === 1) {
          buttonText = '1  ';
        }
        if (buttonText) {
          window.dispatchEvent(new CustomEvent('welno-campaign-button-text', { 
            detail: { text: buttonText } 
          }));
        }
        return newCount;
      });
    }, 1000);
    
    return () => {
      console.log(' [LandingPage]  LandingPage ');
      clearInterval(countdownInterval);
    };
  }, []);

  // URL  
  const userData = useMemo(() => ({
    data: query.get('data') || '', //   
    uuid: query.get('uuid') || query.get('webapp_key') || '', //  
    partner_id: status?.partner_id || query.get('partner') || 'kindhabit', //    ID 
    oid: query.get('oid') || '', //  
    name: query.get('name') || '',
    gender: query.get('gender') || '',
    birth: query.get('birth') || '',
    email: query.get('email') || '',
  }), [query, status]);

  //  Auto Trigger  (      )
  useEffect(() => {
    const autoTrigger = query.get('auto_trigger') === 'true';
    
    // auto_trigger true    (   )
    if (autoTrigger && !isGenerating) {
      handleDirectGenerate();
    }

    //          ( )
    //      ( useEffect )
    if (countdown === null || countdown === 0) {
      const text = status?.requires_payment === false ? 'AI   ' : '';
      window.dispatchEvent(new CustomEvent('welno-campaign-button-text', { 
        detail: { text } 
      }));
    }
  }, [query, isGenerating, status, countdown]); // query      

  const handleDirectGenerate = async () => {
    setIsGenerating(true);
    try {
      const response = await fetch(API_ENDPOINTS.GENERATE_REPORT, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...userData,
          api_key: query.get('api_key') || ''
        }),
      });

      const data = await response.json();
      if (data.success) {
        //  (Result) 
        navigate(`/campaigns/disease-prediction?page=result&oid=${data.oid || userData.oid}`);
      } else {
        alert('   : ' + data.detail);
        setIsGenerating(false);
      }
    } catch (error) {
      console.error('Direct generation failed:', error);
      setIsGenerating(false);
    }
  };

  const handlePayment = useCallback(async () => {
    //    (partner  status   )
    const uuid = userData.uuid;
    // status  status partner_id ,  userData partner_id  
    const partnerId = status?.partner_id || userData.partner_id || 'kindhabit';
    
    if (uuid && partnerId) {
      try {
        const termsCheck = await checkAllTermsAgreement(uuid, partnerId);
        
        if (termsCheck.needsAgreement) {
          //     page=terms 
          navigate(`/campaigns/disease-prediction?page=terms&uuid=${uuid}&partner=${partnerId}&api_key=${query.get('api_key') || ''}`);
          return;
        }
      } catch (error) {
        console.error('[LandingPage]   :', error);
        //      ( )
      }
    }
    
    try {
      // 1.     (    )
      const response = await fetch(API_ENDPOINTS.INIT_PAYMENT, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...userData,
          api_key: query.get('api_key') || '' // API Key  
        }),
      });

      const data = await response.json();

      if (data.success) {
        // 2.     FORM   
        const form = document.createElement('form');
        form.method = 'POST';
        form.action = 'https://mobile.inicis.com/smart/payment/';
        form.acceptCharset = 'euc-kr'; //    euc-kr 

        const params: Record<string, string> = {
          P_INI_PAYMENT: 'CARD', 
          P_MID: data.P_MID,
          P_OID: data.P_OID,
          P_AMT: data.P_AMT,
          P_GOODS: ' ',
          P_UNAME: userData.name || '',
          P_MOBILE: query.get('phone') || '01000000000',
          P_EMAIL: userData.email || '',
          P_NEXT_URL: data.P_NEXT_URL, //    URL 
          P_CHARSET: 'utf8',
          P_TIMESTAMP: data.P_TIMESTAMP,
          P_CHKFAKE: data.P_CHKFAKE,
          P_NOTI: data.P_OID,
          P_RESERVED: 'below1000=Y&vbank_receipt=Y&centerCd=Y&twoticket=OK&cp_view=Y',
        };

        Object.keys(params).forEach((key) => {
          const input = document.createElement('input');
          input.type = 'hidden';
          input.name = key;
          input.value = params[key];
          form.appendChild(input);
        });

        //       
        //  /      
        const currentUrl = window.location.href;
        const landingUrl = currentUrl.replace(/[?&]page=payment/, '').replace(/page=payment[&]?/, '');
        
        document.body.appendChild(form);
        form.submit();
      } else {
        alert('    : ' + data.error);
      }
    } catch (error) {
      console.error('Payment initialization failed:', error);
      alert('   .');
    }
  }, [userData, status, query, navigate]);

  //      
  useEffect(() => {
    if (countdown === 0) {
      //  0   (3  )
      console.log(' [LandingPage]  ,   ');
      setCountdown(null); //  UI 
      handlePayment();
    }
  }, [countdown, handlePayment]);

  //       
  useEffect(() => {
    const handleCampaignClick = () => {
      console.log(' [LandingPage]     ->  ');
      setCountdown(null); //  
      handlePayment();
    };

    window.addEventListener('welno-campaign-click', handleCampaignClick);
    return () => {
      window.removeEventListener('welno-campaign-click', handleCampaignClick);
    };
  }, [handlePayment]);

  return (
    <div className="dp-landing" data-page="payment" key="landing-page-root">
      <main className="dp-content" key="landing-page-main" style={{ paddingBottom: '80px' }}>
        {/*      */}
        <section className="payment-guide" style={{ marginBottom: '15px' }}>
          <div className="price-box">
            <span className="item-name">AI   (PDF)</span>
            <span className="price">7,900</span>
          </div>
          <ul className="benefits">
            <li> 10   4    </li>
            <li>      </li>
            <li>   </li>
            <li>      </li>
            <li>   PDF  </li>
          </ul>
        </section>

        <section className="company-info" style={{ marginTop: '10px', padding: '20px 10px', marginBottom: '10px' }}>
          <h4></h4>
          <p> | 02-6406-3507</p>
          <p> | </p>
          <p> 459-63-00643 |  2023--0121</p>
          <p>   150-29, B01-E167</p>
          <p className="footer-links">
            kkakkung3334@gmail.com | <span></span> | <span></span>
          </p>
        </section>
      </main>
    </div>
  );
};

export default LandingPage;
