import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import DynamicSurvey from '../components/DynamicSurvey';
import { Survey, SurveyResponse, SurveySubmitRequest } from '../types/survey';
import surveyService from '../services/surveyService';

const HealthHabitsPage: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [survey, setSurvey] = useState<Survey | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const loadSurvey = async () => {
      try {
        setLoading(true);
        const surveyData = await surveyService.getSurvey('health-habits');
        setSurvey(surveyData);
      } catch (err) {
        setError('  .');
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    loadSurvey();
  }, []);

  const handleSave = async (response: SurveyResponse) => {
    try {
      const request: SurveySubmitRequest = {
        surveyId: response.surveyId,
        sessionId: response.sessionId,
        answers: response.answers,
        pageId: response.currentPageId
      };
      
      await surveyService.saveSurveyResponse(request);
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleComplete = async (response: SurveyResponse) => {
    try {
      const request: SurveySubmitRequest = {
        surveyId: response.surveyId,
        sessionId: response.sessionId,
        answers: response.answers,
        pageId: response.currentPageId,
        isComplete: true
      };
      
      await surveyService.submitSurvey(request);
      
      //      (URL  )
      const queryString = location.search;
      navigate(`/habits-plan${queryString}`, { state: { surveyResponse: response } });
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleBack = () => {
    // URL     
    const queryString = location.search;
    navigate(`/${queryString}`);
  };

  if (loading) {
    return (
      <div className="questionnaire-container">
        <div className="container bg_xog_yellow">
          <div className="wrapper login">
            <div style={{ textAlign: 'center', padding: '50px' }}>
               ...
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (error || !survey) {
    return (
      <div className="questionnaire-container">
        <div className="container bg_xog_yellow">
          <div className="wrapper login">
            <div style={{ textAlign: 'center', padding: '50px' }}>
              <p>{error || '   .'}</p>
              <button onClick={handleBack} className="question__footer-button">
                
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <DynamicSurvey
      survey={survey}
      onSave={handleSave}
      onComplete={handleComplete}
      onBack={handleBack}
    />
  );
};

export default HealthHabitsPage;