/**
 * AdvancedSearch -     
 *   ,  ,  
 */
import React, { useState, useCallback, useEffect } from 'react';
import { FilterState, HealthStatus } from '../../../types/health';
import './styles.scss';

export interface SavedFilter {
  id: string;
  name: string;
  filters: FilterState;
  isFavorite: boolean;
  createdAt: string;
  lastUsed?: string;
}

export interface AdvancedSearchProps {
  filters: FilterState;
  onFilterChange: (filters: FilterState) => void;
  onSearch: (searchTerm: string) => void;
  savedFilters?: SavedFilter[];
  onSaveFilter?: (name: string, filters: FilterState) => void;
  onLoadFilter?: (filter: SavedFilter) => void;
  onDeleteFilter?: (filterId: string) => void;
  onToggleFavorite?: (filterId: string) => void;
  placeholder?: string;
  showSavedFilters?: boolean;
  showQuickFilters?: boolean;
}

const AdvancedSearch: React.FC<AdvancedSearchProps> = ({
  filters,
  onFilterChange,
  onSearch,
  savedFilters = [],
  onSaveFilter,
  onLoadFilter,
  onDeleteFilter,
  onToggleFavorite,
  placeholder = " ...",
  showSavedFilters = true,
  showQuickFilters = true
}) => {
  const [searchTerm, setSearchTerm] = useState(filters.searchTerm || '');
  const [isExpanded, setIsExpanded] = useState(false);
  const [showSaveDialog, setShowSaveDialog] = useState(false);
  const [filterName, setFilterName] = useState('');

  //   
  const quickFilters = [
    { label: ' 1', key: 'recent_year', filters: { 
      startDate: new Date(Date.now() - 365 * 24 * 60 * 60 * 1000).toISOString().split('T')[0] 
    }},
    { label: ' ', key: 'normal_only', filters: { 
      status: ['' as HealthStatus] 
    }},
    { label: ' ', key: 'warning_only', filters: { 
      status: ['' as HealthStatus] 
    }},
    { label: ' ', key: 'danger_only', filters: { 
      status: ['' as HealthStatus] 
    }},
    { label: '', key: 'blood_test', filters: { 
      category: '' 
    }},
    { label: '', key: 'measurement', filters: { 
      category: '' 
    }}
  ];

  //   
  const handleSearchChange = useCallback((value: string) => {
    setSearchTerm(value);
    onFilterChange({ ...filters, searchTerm: value });
  }, [filters, onFilterChange]);

  //  
  const handleSearch = useCallback(() => {
    onSearch(searchTerm);
  }, [searchTerm, onSearch]);

  // Enter  
  const handleKeyPress = useCallback((e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  }, [handleSearch]);

  //   
  const handleFilterChange = useCallback((key: keyof FilterState, value: any) => {
    const newFilters = { ...filters, [key]: value };
    onFilterChange(newFilters);
  }, [filters, onFilterChange]);

  //   
  const applyQuickFilter = useCallback((quickFilter: typeof quickFilters[0]) => {
    const newFilters = { ...filters, ...quickFilter.filters };
    onFilterChange(newFilters);
  }, [filters, onFilterChange]);

  //  
  const handleSaveFilter = useCallback(() => {
    if (filterName.trim() && onSaveFilter) {
      onSaveFilter(filterName.trim(), filters);
      setFilterName('');
      setShowSaveDialog(false);
    }
  }, [filterName, filters, onSaveFilter]);

  //   
  const handleLoadFilter = useCallback((savedFilter: SavedFilter) => {
    if (onLoadFilter) {
      onLoadFilter(savedFilter);
    }
  }, [onLoadFilter]);

  //  
  const handleResetFilters = useCallback(() => {
    const resetFilters: FilterState = {
      searchTerm: '',
      year: undefined,
      category: 'all',
      startDate: '',
      endDate: '',
      hospitalName: '',
      department: '',
      status: undefined
    };
    setSearchTerm('');
    onFilterChange(resetFilters);
  }, [onFilterChange]);

  return (
    <div className="advanced-search">
      {/*   */}
      <div className="advanced-search__main">
        <div className="search-input-group">
          <input
            type="text"
            className="search-input"
            placeholder={placeholder}
            value={searchTerm}
            onChange={(e) => handleSearchChange(e.target.value)}
            onKeyPress={handleKeyPress}
          />
          <button 
            className="search-button"
            onClick={handleSearch}
            aria-label=""
          >
            
          </button>
          <button
            className={`filter-toggle ${isExpanded ? 'active' : ''}`}
            onClick={() => setIsExpanded(!isExpanded)}
            aria-label=" "
          >
            
          </button>
        </div>
      </div>

      {/*   */}
      {showQuickFilters && (
        <div className="advanced-search__quick-filters">
          <div className="quick-filters-label"> :</div>
          <div className="quick-filters-list">
            {quickFilters.map((quickFilter) => (
              <button
                key={quickFilter.key}
                className="quick-filter-button"
                onClick={() => applyQuickFilter(quickFilter)}
              >
                {quickFilter.label}
              </button>
            ))}
          </div>
        </div>
      )}

      {/*    */}
      {isExpanded && (
        <div className="advanced-search__panel">
          <div className="filter-grid">
            {/*   */}
            <div className="filter-group">
              <label className="filter-label"></label>
              <select
                className="filter-select"
                value={filters.year || ''}
                onChange={(e) => handleFilterChange('year', e.target.value ? parseInt(e.target.value) : undefined)}
              >
                <option value=""></option>
                {Array.from({ length: 10 }, (_, i) => new Date().getFullYear() - i).map(year => (
                  <option key={year} value={year}>{year}</option>
                ))}
              </select>
            </div>

            {/*   */}
            <div className="filter-group">
              <label className="filter-label"> </label>
              <select
                className="filter-select"
                value={filters.category || 'all'}
                onChange={(e) => handleFilterChange('category', e.target.value)}
              >
                <option value="all"></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            {/*   */}
            <div className="filter-group">
              <label className="filter-label"></label>
              <input
                type="date"
                className="filter-input"
                value={filters.startDate || ''}
                onChange={(e) => handleFilterChange('startDate', e.target.value)}
              />
            </div>

            <div className="filter-group">
              <label className="filter-label"></label>
              <input
                type="date"
                className="filter-input"
                value={filters.endDate || ''}
                onChange={(e) => handleFilterChange('endDate', e.target.value)}
              />
            </div>

            {/*   */}
            <div className="filter-group">
              <label className="filter-label"></label>
              <input
                type="text"
                className="filter-input"
                placeholder=" "
                value={filters.hospitalName || ''}
                onChange={(e) => handleFilterChange('hospitalName', e.target.value)}
              />
            </div>

            {/*   */}
            <div className="filter-group">
              <label className="filter-label"></label>
              <select
                className="filter-select"
                value={filters.department || ''}
                onChange={(e) => handleFilterChange('department', e.target.value)}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
          </div>

          {/*   */}
          <div className="filter-actions">
            <button
              className="filter-action-button reset"
              onClick={handleResetFilters}
            >
              
            </button>
            
            {onSaveFilter && (
              <button
                className="filter-action-button save"
                onClick={() => setShowSaveDialog(true)}
              >
                 
              </button>
            )}
          </div>
        </div>
      )}

      {/*   */}
      {showSavedFilters && savedFilters.length > 0 && (
        <div className="advanced-search__saved-filters">
          <div className="saved-filters-header">
            <h4> </h4>
          </div>
          <div className="saved-filters-list">
            {savedFilters.map((savedFilter) => (
              <div key={savedFilter.id} className="saved-filter-item">
                <button
                  className="saved-filter-button"
                  onClick={() => handleLoadFilter(savedFilter)}
                >
                  <span className="saved-filter-name">{savedFilter.name}</span>
                  {savedFilter.isFavorite && <span className="favorite-icon"></span>}
                </button>
                
                <div className="saved-filter-actions">
                  {onToggleFavorite && (
                    <button
                      className="filter-action-icon"
                      onClick={() => onToggleFavorite(savedFilter.id)}
                      aria-label={savedFilter.isFavorite ? " " : " "}
                    >
                      {savedFilter.isFavorite ? '' : ''}
                    </button>
                  )}
                  
                  {onDeleteFilter && (
                    <button
                      className="filter-action-icon delete"
                      onClick={() => onDeleteFilter(savedFilter.id)}
                      aria-label=" "
                    >
                      
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/*    */}
      {showSaveDialog && (
        <div className="save-dialog-overlay">
          <div className="save-dialog">
            <h3> </h3>
            <input
              type="text"
              className="save-dialog-input"
              placeholder="  "
              value={filterName}
              onChange={(e) => setFilterName(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSaveFilter()}
              autoFocus
            />
            <div className="save-dialog-actions">
              <button
                className="save-dialog-button cancel"
                onClick={() => {
                  setShowSaveDialog(false);
                  setFilterName('');
                }}
              >
                
              </button>
              <button
                className="save-dialog-button save"
                onClick={handleSaveFilter}
                disabled={!filterName.trim()}
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdvancedSearch;
